.skew-button {
  cursor: pointer;
  backface-visibility: hidden;
  text-transform: capitalize;
  color: #fff;
  background-color: #0000;
  border: none;
  outline: none;
  padding: 0;
  font-family: Montserrat, sans-serif;
  font-size: 1.3rem;
  display: inline-block;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.skew-button span {
  backface-visibility: hidden;
  width: 100%;
  height: 100%;
  z-index: 3;
  padding: 20px 50px;
  display: block;
  position: relative;
  transform: translate3d(0, 0, 0);
}

.skew-button:before, .skew-button span:before {
  content: "";
  width: 100%;
  height: 50px;
  z-index: 2;
  border: 1px solid;
  border-image: linear-gradient(90deg, #ffe35b 0%, #0ff 100%) 1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)skewX(-20deg);
}

.skew-button span:before {
  transform-origin: center;
  z-index: -1;
  display: block;
  transform: translate(-50%, -50%)skew(-20deg);
}

.skew-button:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 1;
  background-image: linear-gradient(90deg, #ffe35b 0%, #0ff 100%);
  border: 1px solid #0000;
  transition: all .1s ease-out;
  position: absolute;
  top: 0;
  left: 0;
  transform: skewX(-20deg)scaleX(0);
}

.skew-button:hover span:before {
  animation: .5s ease-out forwards fill;
}

.skew-button:hover:after {
  animation: .7s ease-in swoosh;
}

.skew-button:active:before {
  background-color: #030d1b;
}

@keyframes swoosh {
  0% {
    transform-origin: 0;
  }

  20% {
    transform-origin: 0;
    transform: skewX(-20deg)scaleX(1);
  }

  21% {
    transform-origin: 100%;
  }

  50% {
    transform-origin: 100%;
    transform: skewX(-20deg)scaleX(0);
  }
}

@keyframes fill {
  0% {
    background-color: #0e387600;
  }

  20% {
    opacity: .5;
    border-width: 15px;
    border-color: #092248;
  }

  100% {
    opacity: .5;
    background-color: #0e387680;
    border-width: 1px;
  }
}

.card {
  transform-origin: 0 100%;
  height: 12rem;
  border-left: 1px solid #4e5862;
  padding-left: 1em;
  position: relative;
  transform: translateY(100px)scaleY(1);
}

.card:before {
  content: "";
  width: 2rem;
  height: 2rem;
  opacity: 1;
  background: linear-gradient(90deg, #ffe35b 0%, #0ff 100%);
  border: .5em solid #1a1d20;
  border-radius: 100%;
  position: absolute;
  top: 0;
  left: -.9em;
}

.card:hover:before {
  width: 2.2rem;
  height: 2.2rem;
  transition: all .2s ease-in-out;
  top: -.15em;
  left: -.9rem;
}

.card-title {
