  transform: translateY(100px);
}

.card-desc {
  color: #697887;
  opacity: 0;
  margin-left: 20px;
  transform: translateY(100px);
}

.mousey {
  width: 3px;
  height: 35px;
  opacity: .75;
  box-sizing: content-box;
  cursor: pointer;
  border: 2px solid #ffe35b;
  border-radius: 25px;
  padding: 10px 15px;
}

.scroller {
  width: 3px;
  height: 10px;
  background-image: linear-gradient(90deg, #ffe35b 0%, #0ff 100%);
  border-radius: 25%;
  animation-name: scroll;
  animation-duration: 2.2s;
  animation-timing-function: cubic-bezier(.15, .41, .69, .94);
  animation-iteration-count: infinite;
}

@keyframes scroll {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 1;
    transform: translateY(0);
  }

  100% {
    opacity: 0;
    transform: translateY(15px);
  }
}

